.cards {
    padding: 4rem;
    background: #fff;
    padding-top: 90px;

  }
  
  h1 {
    text-align: center;
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1320px;
    width: 90%;
    margin: 0 auto;
  }
  
  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards__items {
    margin-bottom: 24px;
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(37, 47, 65, 0.4);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #253c4c;
    box-sizing: border-box;
    border-radius: 5px 5px 0px 0px;
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  .fa-arrow-right {
    position: absolute;
    bottom: 10px; 
    right: 10px;
    color: black;
  }

  .publication-card {
    max-width: 100%;
    margin: 0 auto;
    background-color: #ffffff; /* Optional: background color for better separation */
    box-sizing: border-box;
}

.publication-content {
    align-items: center;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    padding: 30px;
    max-width: 100%;
    width: 100%;
}

.publication-content h3 {
    font-size: 18px;
    color: grey;
    font-style: italic;
    padding-top: 6px;
}

.publication-content p {
    text-align: justify;
    font-size: 18px;
    line-height: 1.6;
}

.publication-read-btn {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.publication-read-btn .btn-publication {
    border-radius: 2px;
    outline:auto;
    border:#000000;
    cursor: pointer;
    background-color: transparent;
    color: #000000;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
    padding: 12px 26px;
    font-size: 20px;
}

.publication-read-btn .btn-publication:hover {
    background: #000000;
    color: #ffffff;
    transition: all 0.3s ease-out;
}

  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }

